import {
  WS_ORDERS_CONNECTION_SUCCESS,
  WS_ORDERS_CONNECTION_ERROR,
  WS_ORDERS_CONNECTION_CLOSED,
  WS_ORDERS_GET_MESSAGE,
  TWsOrdersActions
} from '../../actions/wsOrders'

export type TWsOrder = {
  ingredients: Array<string>
  status: 'created' | 'pending' | 'done',
  name: string
  number: number
  createdAt: string
  updatedAt: string
  _id: string
}

export type TWsOrdersState = {
  wsConnected: boolean
  orders: Array<TWsOrder>
  error: string | null
  total: number
  totalToday: number
}

export const initialState: TWsOrdersState = {
  wsConnected: false,
  orders: [],
  error: null,
  total: 0,
  totalToday: 0
}

//    WebSocket
export const wsOrdersReducer = (state = initialState, action: TWsOrdersActions): TWsOrdersState => {
  switch (action.type) {
    //      WS_ORDERS_CONNECTION_SUCCESS
    //   wsConnected   true
    case WS_ORDERS_CONNECTION_SUCCESS:
      return {
        ...state,
        error: null,
        wsConnected: true
      }

    //      WS_ORDERS_CONNECTION_ERROR
    //   wsConnected   false     action.payload
    case WS_ORDERS_CONNECTION_ERROR:
      return {
        ...state,
        error: action.payload,
        wsConnected: false
      }

    //      WS_ORDERS_CONNECTION_CLOSED,   
    //   wsConnected   false
    case WS_ORDERS_CONNECTION_CLOSED:
      return {
        ...state,
        error: null,
        wsConnected: false,
        total: 0,
        totalToday: 0
      }

    //      WS_ORDERS_GET_MESSAGE
    //  ,     
    //  messages  ,    
    case WS_ORDERS_GET_MESSAGE:
      return {
        ...state,
        error: null,
        orders: [...action.payload.orders],
        total: action.payload.total,
        totalToday: action.payload.totalToday
      }
    default:
      return state
  }
}
