import jsCookie from 'js-cookie'

type TSet = [
  string,
  any,
  {
    expires: Date
  }?
]

/**
 *   js-cookie  expires  
 */
export const cookie = {
  set (...args: TSet) {
    //  expires  
    args[2] = { expires: new Date(9999999999999), ...args[2] }

    return jsCookie.set(...args)
  },
  get (name: string) {
    return jsCookie.get(name)
  },
  remove (...args: [string]) {
    jsCookie.remove(...args)
  }
}
