/**
 *    
 * @param {object} result
 * @return {Error|Promise}
 */

type TCheckResponse = {
  ok?: boolean
  json: () => any
}

export const checkResponse = async <R>(result: TCheckResponse): Promise<R> => {
  if (!result.ok) {
    throw new Error('  \'ok\'')
  }

  return await result.json()
}
