import React, { useCallback, useEffect, useRef } from 'react'
import { NavLink, Redirect, useLocation } from 'react-router-dom'
import { Input, Button } from '@ya.praktikum/react-developer-burger-ui-components'
import { useDispatch, useSelector } from '../../services/store'
import { TLocation, TTypePassword } from '../../utils/types'
import { getUser, signIn } from '../../services/actions/user'
import Preloader from '../../components/preloader'

const LoginPage = () => {
  const user = useSelector(state => state.user)
  const [emailValue, setEmailValue] = React.useState('')
  const [passwordValue, setPasswordValue] = React.useState('')
  const [typePassword, setTypePassword] = React.useState<TTypePassword>('password')
  const emailRef = useRef<HTMLInputElement>(null)
  const passwordRef = useRef<HTMLInputElement>(null)
  const dispatch = useDispatch()
  const location = useLocation<TLocation>()

  /**
   *    
   */
  const changeTypePassword = () => {
    const newType = typePassword === 'password' ? 'text' : 'password'

    setTypePassword(newType)
  }

  /**
   *    
   */
  const submitForm = useCallback((e) => {
    e.preventDefault()
    dispatch(signIn({
      email: emailValue,
      password: passwordValue
    }))
  }, [dispatch, emailValue, passwordValue])

  //   ,     ,     
  useEffect(() => {
    dispatch(getUser())
  }, [dispatch])

  //   ,   
  if (user.isLoading) {
    return <Preloader fullPage={true} />
  }

  //   ,    
  if (user.data) {
    return (
      <Redirect to={ location.state?.from || '/' } />
    )
  }

  return (
    <div className="form--wr-center">
      <form
        className="form"
        onSubmit={submitForm}
      >
        <p className="text text_type_main-medium mb-6">
          
        </p>
        <div className="mb-6">
          <Input
            type="email"
            placeholder="E-mail"
            ref={emailRef}
            value={emailValue}
            name="email"
            size="default"
            onChange={e => setEmailValue(e.target.value)}
          />
        </div>
        <div className="mb-6">
          <Input
            type={typePassword}
            placeholder=""
            ref={passwordRef}
            value={passwordValue}
            name="password"
            size="default"
            icon={typePassword === 'password' ? 'ShowIcon' : 'HideIcon'}
            onIconClick={changeTypePassword}
            onChange={e => setPasswordValue(e.target.value)}
          />
        </div>
        <div className="mb-20">
          <Button
            type="primary"
            size="large"
            disabled={!emailValue || !passwordValue}
          >
            
          </Button>
        </div>
        <p className="text text_type_main-default text_color_inactive mb-4">
             ? <NavLink to="/register"></NavLink>
        </p>
        <p className="text text_type_main-default text_color_inactive">
           ? <NavLink to="/forgot-password"> </NavLink>
        </p>
      </form>
    </div>
  )
}

export default LoginPage
